body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.wrapper{
  height: 100%;
  width: 380px;
  min-width: 20px;
}

header{
  height: 140.5px;
  border-bottom: 1px solid #E9E9E9;
  position: sticky;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1000;
  background-color: white;
}

.header_logo{
  display: flex;
  align-items: center;
  height: 81.5px;
  border-bottom:1px solid #E9E9E9;
}

.navbar{
  display: flex;
  position: sticky;
  height: 58px;
}

.header_logo_main{
  width: 100%;
  display: flex;
  justify-content: center;
  position: relative;
  align-items: center;
}

.search{
  width: 50px;
  height: 100%;
  display: flex;
  align-items: center;
  position: absolute;
  top: 0px;
  right: 0px;
  justify-content:right;
}

.search:hover{
  cursor: pointer;
}

.close{
  width: 50px;
  height: 100%;
  display: flex;
  align-items: center;
  position: absolute;
  top: 0px;
  right: 0px;
  justify-content:left;
}

.close:hover{
  cursor: pointer;
}

.burger{
  display: none;
  width: 50px;
  height: 100%;
  align-items: center;
  position: absolute;
  top: 0px;
  left: 0px;
  justify-content:left;
}

.burger:hover{
  cursor: pointer;
}

.nav{
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.nav>ul{
  padding-left: 0px;
  list-style: none;
  height: 100%;
  display: flex;
}

.nav>ul>li{
  display: flex;
  margin-right: 2rem;
  line-height: 16px;
  font-weight: 600;
  height: 100%;
  align-items: center;
}

.nav>ul>li:hover{
  cursor: pointer;
}

.o_submenu{
  position: relative;
}

.o_submenu:hover .submenu{
  display: block;
}

.o_submenu::after{
  content: '';
  display: inline-block;
  width: 10px;
  height: 7px;
  line-height: 16px;
  font-weight: 600;
  background-image: url('./icons/arrow.svg');
  background-size: contain;
  background-repeat: no-repeat;
  margin-left: 3px; 
}

.submenu{
  display: none;
  position: absolute;
  border:1px solid #E9E9E9;
  background-color: #fff;
  top: 3.64rem;
  padding: 1.25rem;
  z-index: 10;
}

.submenu>ul{
  list-style: none;
  padding: 0px;
}

.submenu>ul>li{
  position: relative;
  min-width: 136px;
  line-height: 0.813rem;
  font-weight: 400;
  font-size: 0.813rem;
  padding: 8px 0px;
}

.submenu>ul>li:hover{
  color: #969696;
}

.hr{
  border-bottom: 1px solid #E9E9E9;
}

.right_arrow{
  display:flex;
  height: 100%;
  position: absolute;
  right: 0px;
  top: 0px;
  align-items: center;
  justify-content: right;
}

.mobile_header{
  align-items: center;
  height: 81.5px;
  border-bottom: 1px solid #E9E9E9;
}

.fade-element {
  opacity: 1;
  transition: opacity 0.5s ease-in-out;
  visibility: hidden;
  background-color: lightblue;
  text-align: center;
}

.fade-element.hidden {
  opacity: 0;
  visibility: hidden;
}
.fade-element.visible {
  opacity: 1;
  visibility: visible;
}
.hamburger-btn {
  font-size: 1.875rem;
  padding: 10px;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.close-btn {
  margin-top: 20px;
  padding: 5px 10px;
  border: none;
  background-color: #ff6347;
  color: white;
  cursor: pointer;
  font-size: 0.875rem;
  border-radius: 5px;
}

.close-btn:hover {
  background-color: #ff4500;
}

.mobile_navbar{
  display: none;
}

.mobile_dropdown_menu{
  background-color: #fff;
  border-right: 1px solid #E9E9E9;
  border-bottom: 1px solid #E9E9E9;
}

.content{
  margin-top: 3rem;
  display: flex;
}

.posts{
  display: flex;
  flex-wrap: wrap; 
  gap: 3rem; 
  justify-content: space-between;
  position: absolute;
  z-index: -1;
}

.post{
  flex: 1 1 calc(33.333% - 3rem);
  box-sizing: border-box;
}

.author_post{
  display: flex;
  line-height: 0.75rem;
  font-size: 0.75rem;
}

.author{
  font-weight: 600;
}

.dots{
  color: #9B9B9B;
}

.dots::before{
  content: '.';
  display: inline-block;
  width: 15px;
  height: 16px;
  line-height: 1rem;
  font-weight: 600;
  color: #E9E9E9;
  margin-left: 3px;
  margin-top: -14px;
  font-size: 2.5rem;
  text-align: center;
}

.post_image{
  position: relative;
}

.post_image>img{
  width: 100%;
  object-fit: cover;
}

.post_text{
  line-height: 20px;
  font-size: 0.875rem;
  color: #9B9B9B;
}

.post_tags{
  line-height: 0.813rem;
  font-size: 0.813rem;
  color: #EB0028;
  font-weight: 700;
}

.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.modal-content {
  background-color: white;
  padding: 20px;
  border-radius: 8px;
  width: 80%;
  max-width: 600px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  text-align: center;
}

.close-btn {
  background-color: #f44336;
  color: white;
  border: none;
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 1rem;
}

.close-btn:hover {
  background-color: #d32f2f;
}

.main_posts{
  position: relative;
  width: 100%;
}

.search-input-container {
  position: absolute;
  top: 50px;
  right: 20px;
  background-color: white;
  border: 1px solid #ccc;
  padding: 5px;
  border-radius: 4px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  display: flex;
  align-items: center;
}

.search-input-container input {
  border: none;
  padding: 5px;
  font-size: 0.875rem;
  outline: none;
  width: 200px;
}

.search-input-container input:focus {
  border-color: #007bff;
}

@media only screen and (max-width: 1024px) {
  
  .burger{
    display: flex;
  }

  .navbar{
    display: none;
  }

  .mobile_navbar{
    display: block;
    width: calc(100% - 55px);
    position: fixed;
    top: 0px;
    left: 0px;
    background-color: #fff;
    border-right: 1px solid #E9E9E9;
    height: 100%;
  }

  .mobile_navbar>.wrapper{
    width: 20px;
  }

  .mobile_navbar .nav, .mobile_navbar .nav>ul{
    display: block;
  }

  .mobile_navbar .nav{
    padding-left: 20px;
  }

  .mobile_navbar .nav>ul>li{
    padding: 16px 0px;
  }

  .header_logo_main{
    height: 81.5px;
    border-bottom: 1px solid #E9E9E9;
  }

  .o_submenu:hover .submenu{
    display: none;
  }

  .wrapper{
    width: 20px;
  }

  .post{
    flex: 1 1 calc(50% - 3rem);
    box-sizing: border-box;
  }

}

@media only screen and (max-width: 375px) {

  .post{
    flex: 1 1 100%;
    box-sizing: border-box;
  }

}
